/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
:root {
  --header-height: 8rem;
  /*========== Font Family ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --font-family: "Poppins", sans-serif;
  /*========== Font Weight ==========*/
  --fw-medium: 500;
  --fw-semibold: 600;
  --fw-bold: 700;
  /*========== Font Sizing ==========*/
  /* base font size - */
  --fs-base: 1rem;
  /* larger than heading font sizes */
  --fs-giga: 12.5rem;
  --fs-mega: 7.5rem;
  --fs-kilo: 6.25rem;
  /* heading font sizes */
  --fs-h1: 2.5rem;
  --fs-h2: 2rem;
  --fs-h3: 1.75rem;
  --fs-h4: 1.5rem;
  --fs-h5: 1.25rem;
  --fs-h6: 1.125rem;
  /* smaller than heading font sizes */
  --fs-milli: 0.875rem;
  --fs-micro: 0.75rem;
  --fs-nano: 0.5rem;
  /*========== Line Height ==========*/
  /* base line height - */
  --lh-base: 1rem;
  /* larger than heading line height */
  --lh-giga: 10.625rem;
  --lh-mega: 7.5rem;
  --lh-kilo: 4.375rem;
  /* heading line height */
  --lh-h1: 2.5rem;
  --lh-h2: 2rem;
  --lh-h3: 1.75rem;
  --lh-h4: 1.5rem;
  --lh-h5: 1.375rem;
  --lh-h6: 1.25rem;
  /* smaller than heading line height */
  --lh-milli: 0.75rem;
  --lh-micro: 0.625rem;
  --lh-nano: 0.5rem;
  /*========== Letter Spacing ==========*/
  --ls-giga: 0.125rem;
  --ls-mega: 0.094rem;
  --ls-kilo:0.0625rem;
  --ls-milli: 0.05rem;
  --ls-micro: 0.03125rem;
  /*========== Margenes ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --m-0-5: 0.5rem;
  --m-0-75: 0.75rem;
  --m-1: 1rem;
  --m-1-5: 1.5rem;
  --m-2: 2rem;
  --m-2-5: 2.5rem;
  /*========== Z-Index ==========*/
  --z-fixed: 100;
  --body-text: var(--fs-base);
  --display-text: var(--fs-giga);
  --button-text: var(--fs-h6);
}
:root {
  --clr-white: hsl(0, 0%, 100%);
  --clr-black: hsl(0, 0%, 0%);
  --clr-base: hsl(0, 0%, 100%);
  --clr-base-lt: hsl(0, 0%, 50%);
  --clr-base-dk: hsl(0, 0%, 67%);
  --clr-primary: hsl(156, 64%, 41%);
  --clr-secondary: hsl(48, 100%, 50%);
  --clr-secondary-lt: hsl(52, 100%, 63%);
  --clr-tertiary: hsl(344, 97%, 53%);
  --dark-card-hover: hsla(0, 0%, 0%, 10%);
  --light-text1: var(--clr-black);
  --light-text2: hsl(230, 17%, 14%);
  --dark-card: var(--clr-black);
  --dark-bg: var(--clr-black);
  --light-link-bg: hsla(0, 0%, 0%, 10%);
  --dark-link-bg: hsla(0, 0%, 100%, 10%);
  --light-box-bg: hsla(0, 0%, 0%, 10%);
  --dark-box-bg: hsla(0, 0%, 100%, 10%);
  --light-bg: var(--clr-base);
  --dark-text1: var(--clr-base);
  --dark-text2: var(--clr-base-lt);
  --background: var(--dark-bg);
  --text-color: var(--clr-base);
  --text-color2: var(--dark-text2);
  --text-color-lt: var(--clr-white);
  --text-color-dk: var(--clr-black);
  --nav-link-hover-bg: var(--dark-link-bg);
  --button-primary-color: var(--clr-primary);
  --button-secondary-color: var(--clr-base-lt);
  --timer-bg: var(--dark-bg);
  --box-bg: var(--dark-box-bg);
  --card-overlay: linear-gradient(180deg, hsla(0, 0%, 0%, 0) 80%, hsl(0, 0%, 0%) 100%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text1);
    --text-color2: var(--dark-text2);
    --timer-bg: var(--dark-bg);
    --nav-link-hover-bg: var(--dark-link-bg);
  }
}
@media (prefers-color-scheme: light) {
  :root {
    --background: var(--light-bg);
    --text-color: var(--light-text1);
    --text-color2: var(--light-text2);
    --timer-bg: var(--light-bg);
    --box-bg: var(--light-box-bg);
    --nav-link-hover-bg: var(--light-link-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text1);
  --text-color2: var(--light-text2);
  --timer-bg: var(--light-bg);
  --box-bg: var(--light-box-bg);
  --nav-link-hover-bg: var(--light-link-bg);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --timer-bg: var(--dark-bg);
  --box-bg: var(--dark-box-bg);
  --nav-link-hover-bg: var(--dark-link-bg);
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
@media (min-width: 71.875em) {
  html {
    font-size: 100%;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-family);
  background: var(--background);
  color: var(--text-color);
  font-size: var(--body-text);
}

.icon {
  font-size: 1.6rem;
  line-height: var(--lh-base);
  padding: 0.5rem;
  transition: all 0.3s ease-out;
  cursor: pointer;
  border-radius: 0.5rem;
}
.icon:hover,
.icon :active {
  color: var(--text-color);
  background: var(--dark-card-hover);
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:hover,
a:active {
  color: var(--clr-primary);
}

.heading--1 {
  font-size: var(--fs-h1);
}
.heading--2 {
  font-size: var(--fs-h2);
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

/*============  Button =================*/
.btn {
  font-weight: var(--fw-semibold);
  font-size: var(--button-text);
  line-height: var(--lh-h5);
  letter-spacing: var(-ls-kilo);
  padding: 1.5rem 2.5rem;
  border: none;
  border-radius: 0.625rem;
  color: var(--text-color-lt);
  cursor: pointer;
}
.btn--primary {
  background-color: var(--clr-primary);
}
.btn--secondary {
  background-color: #1a1a1a;
}
.btn--link {
  font-size: var(--fs-milli);
  color: #ababab;
  text-transform: uppercase;
}
.btn--link:active, .btn--link:focus, .btn--link:hover {
  color: var(--clr-primary);
}

.card {
  display: inline-flex;
  flex-direction: column;
  padding: 1rem 1.6rem;
  background: #1a1a1a;
  border-radius: 1.25rem;
}
.card__title {
  font-weight: var(--fw-semibold);
  font-size: var(--fs-h1);
  line-height: var(--lh-h1);
  letter-spacing: var(--ls-kilo);
}
.card__subtitle {
  font-size: 13px;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: var(--ls-kilo);
}
.card-group {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.tutorial-card {
  max-width: 30%;
  min-width: 12.5rem;
}
.tutorial-card__img {
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
}
.tutorial-card__caption {
  transform: translateY(-40px);
  display: flex;
  flex-direction: column;
  row-gap: 0.9375rem;
  padding-left: 1.25rem;
}
.tutorial-card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tutorial-card__title {
  font-weight: var(--fw-semibold);
  font-size: var(--fs-h1);
  color: var(--text-color-lt);
  line-height: var(--lh-h1);
  letter-spacing: var(--ls-micro);
}
.tutorial-card__timer {
  font-weight: var(--fw-semibold);
  font-size: var(--fs-h5);
  line-height: var(--lh-h6);
  letter-spacing: var(--ls-mega);
  padding: 0.625rem;
  border-radius: 50vh 0 0 50vh;
  background-color: var(--timer-bg);
}
.tutorial-card__subdesc {
  font-weight: normal;
  font-size: var(--fs-milli);
  line-height: 0.875rem;
  letter-spacing: var(--ls-micro);
  opacity: 0.67;
}

.img-container {
  position: relative;
}
.img-container::after {
  content: "";
  position: absolute;
  inset: 0px;
  background: var(--card-overlay);
}

/*=========== HEADER ==============*/
.header {
  height: var(--header-height);
  display: flex;
  align-items: center;
}
.header .nav {
  display: flex;
  align-items: center;
  width: 100vw;
}
.header .nav__logo {
  color: var(--text-color);
  font-size: var(--fs-h6);
  line-height: var(--lh-h6);
  letter-spacing: var(--ls-milli);
  text-transform: uppercase;
  text-decoration: none;
}
.header .nav__menu {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: end;
  margin: 1rem;
}
.header .nav__list {
  display: flex;
  gap: 3.125rem;
  align-items: center;
  margin: 0.5rem;
  list-style: none;
  text-decoration: none;
}
.header .nav__item {
  font-size: var(--fs-milli);
  line-height: var(--lh-milli);
  letter-spacing: var(--ls-mega);
  color: var(--text-color2);
  text-transform: uppercase;
  padding: 0.5rem;
  cursor: pointer;
  transition: background 0.3s ease-in;
}
.header .nav__item:hover,
.header .nav__item :active {
  color: var(--clr-primary);
  background: var(--nav-link-hover-bg);
  border-radius: 0.25rem;
}
.header .nav__close {
  display: none;
}
.header .nav__btns:hover,
.header .nav__btns :active,
.header .nav__btns :focus {
  color: var(--clr-secondary);
}

/*=========== HERO ==============*/
.hero__container {
  display: grid;
  grid-template-columns: repeat(11, minmax(4rem, 1fr));
  grid-template-rows: 3.125rem repeat(5, minmax(6.25rem, 1fr));
  gap: 0.5rem;
}
.hero__header {
  grid-column: 1/-1;
  grid-row: 2/-1;
  display: grid;
  grid-template-columns: repeat(11, minmax(4rem, 1fr));
  grid-template-rows: repeat(4, 1fr);
}
.hero-right {
  grid-column: 8/-1;
  grid-row: 1/-1;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
}
.hero-left {
  grid-column: 1/8;
  grid-row: 1/-1;
}
.hero__img {
  grid-column: 1/-1;
  grid-row: 1/-1;
  background-image: url("/app/img/hero-img3.png");
  background-position: 35% 45%;
  height: 44rem;
  z-index: 1;
}
.hero__card-group {
  grid-column: 6/-1;
  grid-row: 2/4;
  z-index: 10;
  color: var(--text-color-lt);
}
.hero__tag {
  display: flex;
  align-items: center;
  color: var(--text-color-lt);
  background: #1a1a1a;
  padding: 0.5rem;
  border-radius: 50vh;
  grid-column: 1/5;
  grid-row: 1/2;
}
.hero__tag--status {
  flex-basis: 50px;
  font-size: var(--fs-micro);
  font-weight: var(--fw-semibold);
  text-align: center;
  text-transform: uppercase;
  line-height: var(--fs-h6);
  letter-spacing: var(--ls-mega);
  background: var(--clr-primary);
  padding: 0.5rem;
  border-radius: 10vh;
}
.hero__tag--info {
  padding: 0.5rem;
}
.hero__heading {
  font-size: var(--display-text);
  line-height: var(--lh-giga);
  letter-spacing: -0.125rem;
}
.hero__desc {
  line-height: 1.8125rem;
  letter-spacing: var(--ls-micro);
  color: gray;
  margin: 3.125rem 0;
}
.hero__btns {
  display: flex;
  align-items: center;
  gap: 1.875rem;
}

.timer {
  color: var(--clr-secondary);
}

.calories {
  color: var(--clr-tertiary);
}

/*========== EXERCISES ===========*/
.exercises {
  width: 100%;
  margin: 6.25rem 0;
}
.exercises__container {
  width: 100%;
}
.exercises__header {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.tutorials {
  width: 100%;
  display: flex;
  flex-grow: 1;
  flex-shrink: 1;
  flex-wrap: wrap;
  gap: 2.5rem;
  margin: 3.125rem 0;
}

/*=========== TRAINERS ==============*/
.trainers__container {
  display: grid;
  grid-template-columns: repeat(11, minmax(min-content, 1fr));
  grid-template-rows: repeat(8, minmax(3.125rem, 1fr));
}
.trainers__header {
  position: relative;
  grid-column: 1/6;
  grid-row: 1/4;
  z-index: 10;
}
.trainers-box {
  grid-column: 4/-1;
  grid-row: 1/4;
  background: var(--box-bg);
  display: flex;
  align-items: center;
  justify-content: center;
}
.trainers-box__content {
  margin: auto 7.5rem;
  transform: translateX(20%);
}
.trainers__heading {
  font-weight: var(--fw-semibold);
  font-size: var(--fs-mega);
  line-height: var(--lh-mega);
  letter-spacing: -2px;
}
.trainers__desc {
  font-size: var(--fs-h4);
  line-height: var(-lh-h1);
  letter-spacing: var(--ls-micro);
  opacity: 0.7;
  margin-bottom: 2.5rem;
}
.trainers__artwork {
  position: absolute;
  left: -5%;
  bottom: 0%;
  z-index: -1;
}

.trainer__img {
  margin-bottom: 0.625rem;
}
.trainer__name {
  font-weight: normal;
  font-weight: var(--fw-medium);
  font-size: var(--fs-h5);
  line-height: 1.5625rem;
  letter-spacing: var(--ls-micro);
}
.trainer__title {
  font-weight: normal;
  font-size: var(--fs-milli);
  line-height: var(--fs-milli);
  letter-spacing: var(--ls-micro);
  margin-top: 0.3125rem;
  opacity: 0.5;
}
.trainer-1 {
  grid-column: 1/4;
  grid-row: 4/-1;
}
.trainer-2 {
  grid-column: 6/-1;
  grid-row: 4/7;
  transform: translateY(-10%);
}
.trainer-3 {
  grid-column: 4/8;
  grid-row: 7/-1;
}

/*=========== PROGRAMS ==============*/
.programs {
  margin: 6.25rem 0;
}

.program__list {
  display: flex;
  flex-direction: column;
  row-gap: 0.625rem;
  list-style: none;
}
.program__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0;
  border-bottom: 0.5px solid #999999;
}
.program-content {
  flex-basis: 40%;
  margin: auto;
}
.program__num {
  font-weight: var(--fw-semibold);
  font-size: var(--fs-h4);
  line-height: var(--lh-h2);
  opacity: 0.34;
}
.program__title {
  font-weight: normal;
  font-size: var(--fs-h1);
  line-height: var(--lh-kilo);
  letter-spacing: var(--ls-milli);
}
.program__subdesc {
  font-size: var(--fs-milli);
  line-height: var(--lh-h5);
  letter-spacing: var(--ls-micro);
  opacity: 0.5;
}